// Obtener todos los elementos que pueden ser arrastrados (profesores)
const draggableProfessors = document.querySelectorAll('.professor');

// Añadir a cada profesor los listeners de eventos
draggableProfessors.forEach(professor => {
    professor.addEventListener('dragstart', dragStart);
    professor.addEventListener('dragend', dragEnd);
});

// Definir la función cuando inicia el arrastre
function dragStart() {
    this.classList.add('dragging');
}

// Definir la función cuando termina el arrastre
function dragEnd() {
    this.classList.remove('dragging');
}

// Obtener los elementos donde se pueden soltar (módulos)
const technicalModule = document.getElementById('technical-modules');
const secondaryModule = document.getElementById('secondary-modules');

// Añadir a cada módulo los listeners de eventos
technicalModule.addEventListener('dragover', dragOverTechnical);
technicalModule.addEventListener('dragenter', dragEnter);
technicalModule.addEventListener('dragleave', dragLeave);
technicalModule.addEventListener('drop', dropTechnical);

secondaryModule.addEventListener('dragover', dragOverSecondary);
secondaryModule.addEventListener('dragenter', dragEnter);
secondaryModule.addEventListener('dragleave', dragLeave);
secondaryModule.addEventListener('drop', dropSecondary);

// Definir la función cuando el elemento arrastrable está encima del módulo técnico
function dragOverTechnical(e) {
    e.preventDefault();
}

// Definir la función cuando el elemento arrastrable entra en el módulo técnico
function dragEnter() {
    this.classList.add('hovered');
}

// Definir la función cuando el elemento arrastrable sale del módulo técnico
function dragLeave() {
    this.classList.remove('hovered');
}

// Definir la función cuando el elemento arrastrable se suelta en el módulo técnico
function dropTechnical() {
    this.classList.remove('hovered');
    const draggable = document.querySelector('.dragging');
    const draggableParent = draggable.parentElement;

    if (draggableParent.classList.contains('technician-professors')) {
        this.appendChild(draggable);
    }
}

// Definir la función cuando el elemento arrastrable está encima del módulo de secundaria
function dragOverSecondary(e) {
    e.preventDefault();
}

// Definir la función cuando el elemento arrastrable entra en el módulo de secundaria
function dropSecondary() {
    this.classList.remove('hovered');
    const draggable = document.querySelector('.dragging');
    const draggableParent = draggable.parentElement;

    if (draggableParent.classList.contains('secondary-professors')) {
        this.appendChild(draggable);
    }
}
